<template>
    <div class="register">
      <h1>{{ msg }}</h1>
      <Button type="primary" @click='login'>登陆界面</Button>
    </div>
</template>

<script>
    export default {
        name: "register",
        data(){
          return{
            msg:"我是注册页面"
          }
        },
      methods:{
        login:function () {
          this.$router.push('login')
        }
      }
    }
</script>

<style scoped>

</style>



