<template>
  <div class="layout">
    <Layout>
      <Header>
        <my-header></my-header>
      </Header>
      <Content>
        <router-view></router-view>
      </Content>
      <Footer>
        <my-footer></my-footer>
      </Footer>
    </Layout>
  </div>
</template>

<script>

  import myHeader from './header'
  import myFooter from './footer'

  export default {
    name: 'layout',
    data() {
      return {
        value1: 0
      }
    },
    components: {
      'my-header': myHeader,
      'my-footer': myFooter,
    }
  }
</script>

