<template>
  <div class="hello">
    <Carousel v-model="value1" loop>
      <CarouselItem>
        <div class="demo-carousel">{{ msg }} 1</div>
      </CarouselItem>
      <CarouselItem>
        <div class="demo-carousel">{{ msg }} 2</div>
      </CarouselItem>
      <CarouselItem>
        <div class="demo-carousel">{{ msg }} 3</div>
      </CarouselItem>
      <CarouselItem>
        <div class="demo-carousel">{{ msg }} 4</div>
      </CarouselItem>
    </Carousel>
    <Table :columns="columns1" :data="data1"></Table>
    <video-player  class="video-player-box"
                   ref="videoPlayer"
                   :options="playerOptions"
                   :playsinline="true"
                   customEventName="customstatechangedeventname"

                   @play="onPlayerPlay($event)"
                   @pause="onPlayerPause($event)"
                   @statechanged="playerStateChanged($event)"
                   @ready="playerReadied">
    </video-player>

  </div>
</template>


<script>
  import 'video.js/dist/video-js.css'
  import { videoPlayer } from 'vue-video-player'
  export default {
    name: 'HelloWorld',
    data() {
      return {
        msg: '我是页面的主要内容 啦啦啦啦啦阿拉啦',
        value1: 1,
        columns1: [
          {
            title: 'Name',
            key: 'name'
          },
          {
            title: 'Age',
            key: 'age'
          },
          {
            title: 'Address',
            key: 'address'
          }
        ],
        data1: [
          {
            name: 'John Brown',
            age: 18,
            address: 'New York No. 1 Lake Park',
            date: '2016-10-03'
          },
          {
            name: 'Jim Green',
            age: 24,
            address: 'London No. 1 Lake Park',
            date: '2016-10-01'
          },
          {
            name: 'Joe Black',
            age: 30,
            address: 'Sydney No. 1 Lake Park',
            date: '2016-10-02'
          },
          {
            name: 'Jon Snow',
            age: 26,
            address: 'Ottawa No. 2 Lake Park',
            date: '2016-10-04'
          }
        ],
        playerOptions: {
          muted: true,
          language: 'zh-cn',
          playbackRates: [0.7, 1.0, 1.5, 2.0],
          sources: [{
            type: "video/mp4",
            src: "https://cdn.theguardian.tv/webM/2015/07/20/150716YesMen_synd_768k_vp8.webm"
          }],
          poster: "https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1522944681922&di=b10d6ba330b7a95b4f019cd666408609&imgtype=0&src=http%3A%2F%2Fpic1.5442.com%2F2015%2F0103%2F01%2F11.jpg",
        }
      }
    },
    mounted() {
      console.log('这是当前播放器实例', this.player)
    },
    computed: {
      player() {
        return this.$refs.videoPlayer.player
      }
    },
    methods: {
      // listen event
      onPlayerPlay(player) {
         console.log("开始播放")
      },
      onPlayerPause(player) {
         console.log("暂停播放")
         console.log(player)
      },
      playerStateChanged(playerCurrentState) {
         console.log("播放状态")
      },
      playerReadied(player) {
        console.log('准备播放就绪')
      }
    },
    components:{
      'video-player':videoPlayer
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .hello {
    width: 1200px;
    margin: 0 auto;
  }
  .video{
    width: 400px;
    height: 300px;
  }
  .demo-carousel {
    height: 200px;
    background: #2b85e4;
    line-height: 200px;
    text-align: center;
    color: #fff;
  }
</style>



