<template>
  <div class="header">
    <router-link to="/">
      <img src="../assets/wandou.png" alt="logo">
    </router-link>
    <Button type="primary" @click='echart'>echart</Button>
    <Button type="primary" @click='login'>登陆</Button>
    <Button type="primary" @click='register'>注册</Button>
  </div>
</template>

<script>
  export default {
    name: 'myHeader',
    data() {
      return {
        msg: '头部信息'
      }
    },
    methods: {
      login: function () {
        console.log(this.$router.push('login'))
      },
      register: function () {
        this.$router.push('register')
      },
      echart:function () {
        this.$router.push('echart')
      }
    }
  }
</script>

<style scoped>
  .header {
    width: 1200px;
    margin: 0 auto;
  }

  button {
    float: right;
    margin-top: 15px;
    margin-right: 10px;
  }

  img {
    width: 150px;
  }

  h1 {
    display: inline-block;
    color: #fff
  }
</style>



